body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/bg.dd57b49a.jpg);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  padding: 5px;
  border-radius: 3px;
  background: #e1e1e1;
}
.chat {
  -webkit-filter: none;
          filter: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 500px;
  margin: 0 auto 20px;
  border: 1px solid #ccc;
  padding: 10px 0px 5px 10px;
  border-radius: 5px;
  background: url(/static/media/chat.c6da95b7.jpg);
}
.chat_messages {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: scroll;
  -webkit-scroll-snap-type: none;
      -ms-scroll-snap-type: none;
          scroll-snap-type: none;
  height: 60vh;
  padding-right: 10px;
}
.message {
  border: 1px solid #ccc;
  margin: 10px 0;
  padding: 7px;
  border-radius: 5px;
}
.left {
  -webkit-align-self: flex-start;
          align-self: flex-start;
  background: #fff;
}
.right {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  background: rgb(219, 255, 187);
}

.chat_input {
  margin: 10px 10px 10px 0;
  height: 27px;
  font-size: 15px;
  border-radius: 3px;
  border: 2px solid #ccc;
  padding-left: 5px;
}
.chat_selector {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 30px;
  cursor: pointer;
}
.buttons {
  position: absolute;
  top: 20px;
  right: 20px;
  display: -webkit-flex;
  display: flex;
}
.buttons * {
  cursor: pointer;
  padding: 0px 5px;
}
button {
  font-size: 20px;
  background: #fff;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

